.mega {
    overflow: hidden;

    &__bg {
        position: absolute;
        width: 1200px;
        height: 355px;
        left: 47%;
        transform: translateX(-50%);
        bottom: -65px;
        z-index: -1;

        @include vp-767 {
            max-width: 100%;
            height: auto;
            aspect-ratio: 1000/350;
            width: 100%;
            left: 50%;
        }
    }

    &__bg img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__small1 {
        position: absolute;
        width: 130px;
        height: 105px;
        left: 67px;
        top: 67px;

        @include vp-767 {
            display: none;
        }
    }

    &__small1 img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__small2 {
        position: absolute;
        width: 220px;
        height: 230px;
        left: 0;
        top: 140px;

        @include vp-1023 {
            width: 150px;
            height: 150px;
            top: 200px;
        }

        @media screen and (max-width: 520px) {
            width: 120px;
            height: 120px;
            top: 130px;
            left: -20px;
        }
    }

    &__small2 img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__small3 {
        position: absolute;
        width: 220px;
        height: 260px;
        right: 0;
        top: 65px;

        @include vp-1023 {
            width: 150px;
            height: 200px;
        }

        @media screen and (max-width: 520px) {
            width: 90px;
            height: 100px;
            right: -20px;
            top: 120px;
        }
    }

    &__small3 img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__small4 {
        position: absolute;
        width: 170px;
        height: 170px;
        right: 140px;
        bottom: 0;

        @include vp-1279 {
            right: 0;
        }

        @include vp-767 {
            width: 140px;
            height: 140px;
            right: -25px;
        }

        @media screen and (max-width: 520px) {
            width: 100px;
            height: 100px;
            bottom: -25px;
        }
    }

    &__small4 img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__bg-mobile {
        display: none;

        @include vp-767 {
            display: block;
            position: absolute;
            bottom: 0;
            left: 10px;
            width: 100px;
            height: 100px;
        }

        @media screen and (max-width: 520px) {
            width: 70px;
            height: 70px;
        }

        @include vp-389 {
            left: 0;
        }
    }

    &__wrapper {
        position: relative;
        padding: 0 70px;
        overflow: hidden;
        @media screen and (max-width: 1470px) {
            padding: 0px 40px;
        }
    
        @media screen and (max-width: 1024px) {
            padding: 0px 20px;
        }

        @include vp-389 {
            padding: 0px 10px;
        }
    }

    &__info {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: 70px;

        @include vp-1023 {
            margin-top: 50px;
        }
    }

    &__title {
        font-family: Coolvetica;
        font-weight: 400;
        font-style: Regular;
        font-size: 80px;
        leading-trim: NONE;
        line-height: 90%;
        letter-spacing: 0%;
        text-align: center;
        color: rgba(107, 82, 221, 1);
        margin: 0 0 20px;

        @include vp-1023 {
            font-size: 70px;
        }

        @media screen and (max-width: 520px) {
            font-size: 50px;
        }
    }

    &__text {
        font-family: Inter;
        font-weight: 500;
        font-style: Medium;
        font-size: 18px;
        leading-trim: NONE;
        line-height: 140%;
        letter-spacing: 0%;
        text-align: center;
        color: rgba(56, 56, 56, 1);
        margin: 0 0 23px;

        @include vp-1023 {
            font-size: 16px;
        }

        @media screen and (max-width: 520px) {
            display: none;
        }

        &--mobile {
            display: none;

            
            @media screen and (max-width: 520px) {
                display: block;
                color: rgba(60, 43, 139, 1);
                font-family: Inter;
                font-weight: 400;
                font-style: Regular;
                font-size: 14px;
                leading-trim: NONE;
                line-height: 80%;
                letter-spacing: 0%;
                text-align: center;
                vertical-align: middle;

            }
        }
    }

    &__info span {
        display: block;
        background: rgba(107, 82, 221, 1);
        border-radius: 100px;
        padding: 5px 10px 10px;
        font-family: Coolvetica;
        font-weight: 400;
        font-style: Regular;
        font-size: 30px;
        leading-trim: NONE;
        line-height: 80%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: rgba(255, 255, 255, 1);
        transform: rotate(1.37deg);

        @include vp-1023 {
            font-size: 24px;
        }

        @media screen and (max-width: 520px) {
            font-size: 16px;
            transform: rotate(3deg);
        }
    }

    &__image {
        width: 500px;
        height: 450px;
        position: relative;
        top: 70px;
        margin-top: -83px;

        @media screen and (max-width: 520px) {
            margin-top: -20px;
            width: 290px;
            height: 270px;
        }
    }

    &__image img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}