.custom-checkbox {
  position: relative;

  label {
    display: inline-flex;
    align-items: center;
    cursor: pointer;
  }

  // сам квадратик чекбокса
  &__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;

    width: 20px;
    height: 20px;
    margin-right: 8px;

    border-radius: 4px;
    border: 2px solid #ccc;
    background-color: #fff;
    transition: all 0.3s ease;

    img, svg {
      width: 12px;
      height: 12px;
      opacity: 0;
      transform: scale(0.6);
      transition: all 0.25s ease;
    }
  }

  // скрываем нативный input
  input {
    position: absolute;
    opacity: 0;
    pointer-events: none;
  }

  // когда активен
  input:checked + .custom-checkbox__icon {
    background-color: #b3e100;
    border-color: #b3e100;

    img, svg {
      opacity: 1;
      transform: scale(1);
    }
  }

  // текст рядом с чекбоксом (всегда виден)
  &__text {
    font-weight: 400;
    font-size: 14px;
    line-height: 1.3;
    color: #383838;

    a {
      color: #383838;
      text-decoration: underline;
      transition: opacity 0.3s;

      &:hover {
        opacity: 0.7;
      }
    }
  }
}