.faq {
    overflow: hidden;
    position: relative;
    z-index: 1;
    margin-top: -60px;
    &__wrapper {
        padding: 90px 70px;
        overflow: hidden;
        background: rgba(185, 227, 131, 1);
        position: relative;
        border-radius: 60px 60px 0 0;
        @media screen and (max-width: 1470px) {
            padding: 90px 40px;
        }
    
        @media screen and (max-width: 1024px) {
            padding: 60px 20px;
            border-radius: 32px 32px 0 0;
        }

        @include vp-389 {
            padding: 60px 10px;
        }
    }

    &__bg1 {
        position: absolute;
        width: 150px;
        height: 150px;
        top: 52px;
        left: 720px;

        @include vp-1279 {
            left: 500px;
        }

        @include vp-1023 {
            left: 350px;
        }

        @include vp-767 {
            width: 80px;
            height: 80px;
            left: 250px;
        }

        @media screen and (max-width: 520px) {
            width: 60px;
            height: 60px;
            left: 170px;
            top: 45px;
        }

        
        @include vp-389 {
            left: 150px;
        }
    }

    &__bg1 img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    
    &__bg2 {
        position: absolute;
        width: 260px;
        height: 260px;
        top: 0px;
        left: 950px;

        @include vp-1279 {
            left: 800px;
            width: 200px;
            height: 200px;
        }

        @include vp-1023 {
            left: 700px;
            width: 150px;
            height: 150px;
        }

        @media screen and (max-width: 950px) {
            left: 600px;
        }

        @include vp-767 {
            display: none;
        }
    }

    &__bg2 img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__m {
        display: none;

        @include vp-767 {
            display: block;
            width: 140px;
            height: 140px;
            position: absolute;
            top: 0px;
            right: 60px;
        }

        @media screen and (max-width: 520px) {
            width: 100px;
            height: 100px;
            top: 0px;
        }

        @include vp-389 {
            right: 20px;
            top: 0px;
        }
    }

    &__m img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__bg3 {
        position: absolute;
        width: 110px;
        height: 110px;
        top: 180px;
        right: 0;

        @include vp-1023 {
            width: 80px;
            height: 80px;
            top: 150px;
        }

        @include vp-767 {
            width: 50px;
            height: 50px;
            top: 120px;
        }

        @media screen and (max-width: 520px) {
            width: 30px;
            height: 30px;
            top: 100px;
        }
    }

    &__bg3 img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__title {
        font-family: Coolvetica;
        font-weight: 400;
        font-style: Regular;
        font-size: 60px;
        leading-trim: NONE;
        line-height: 90%;
        letter-spacing: 0%;
        color: rgba(81, 60, 176, 1);
        margin: 0 0 30px;

        @include vp-767 {
            font-size: 40px;
            margin-bottom: 20px;
        }

        @media screen and (max-width: 520px) {
            font-size: 30px;
        }
    }

    &__text {
        font-family: Inter;
        font-weight: 500;
        font-style: Medium;
        font-size: 18px;
        leading-trim: NONE;
        line-height: 140%;
        letter-spacing: 0%;
        color: rgba(56, 56, 56, 1);
        margin: 0 0 60px;

        @include vp-767 {
            font-size: 16px;
            margin-bottom: 30px;
        }

        @media screen and (max-width: 520px) {
            margin-bottom: 20px;
            color: rgba(60, 43, 139, 1);
            font-size: 14px;
        }
    }

    .accordion {
        display: flex;
        flex-direction: column;
        gap: 10px;
        max-width: 100%;
        text-align: left !important;
        margin-bottom: 60px;

        @media screen and (max-width: 520px) {
            margin-bottom: 30px;
        }
    }

    .accordion__item {
        background: rgba(255, 255, 255, 1);
        border-radius: 30px;
        padding: 30px 25px 27px 36px;

        
        @include vp-767 {
            padding: 20px 27px 20px 31px;
            border-radius: 20px;
        }
    }

    .accordion__button {
        cursor: pointer;
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 20px;
    }

    .accordion__button:hover {
        opacity: 0.8;
    }

    .accordion__button span {
        font-family: Inter;
        font-weight: 700;
        font-style: Bold;
        font-size: 18px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        color: rgba(60, 43, 139, 1);
        display: block;
        text-align: left;
        transition: .3s;

        @include vp-767 {
            font-size: 16px;
        }

        @media screen and (max-width: 520px) {
            max-width: 80%;
        }
    }

    .accordion__button svg {
        width: 37px;
        height: 37px;
        flex-shrink: 0;
        transition: all 0.3s ease;

        @include vp-767 {
            width: 30px;
            height: 30px;
        }

        @media screen and (max-width: 520px) {
            width: 25px;
            height: 25px;
        }
    }

    .accordion__button svg .line-vertical {
        transition: opacity 0.3s ease, transform 0.3s ease;
        opacity: 1;
        transform: scaleY(1);
        transform-origin: center;
    }

    .accordion__item.is-active .accordion__button svg .line-vertical {
        opacity: 0;
        transform: scaleY(0); 
    }

    .accordion__content {
        max-height: 0;
        overflow: hidden;
        opacity: 0;
        max-width: 670px;
        width: 100%;
        transition: max-height 0.4s ease, opacity 0.4s ease;
    }

    .accordion__item.is-active .accordion__content {
        opacity: 1;
    }

    .accordion__inner {
        font-family: Inter;
        font-weight: 500;
        font-style: Medium;
        font-size: 16px;
        leading-trim: NONE;
        line-height: 140%;
        letter-spacing: 0%;
        color: rgba(56, 56, 56, 1);
        display: block;
        text-align: left;
        margin-top: 20px;

        @include vp-767 {
            font-size: 14px;
        }

        p {
            margin: 0;
        }

        a {
            display: inline-block;
            text-decoration: underline;
            color: inherit;
            transition: .3s;
        }

        a:hover {
            opacity: 0.8;
        }

        button {
            text-decoration: underline;
            transition: .3s;
        }

        button:hover {
            opacity: 0.8;
        }
    }

    &__info {
        background: rgba(255, 255, 255, 1);
        border-radius: 37px;
        padding: 37px 40px 39px 40px;
        display: flex;
        justify-content: space-between;
        gap: 15px;
        align-items: center;

        @include vp-767 {
            flex-direction: column;
            gap: 20px;
            padding: 30px;
            border-radius: 20px;
            background: rgba(60, 43, 139, 1);
        }

        @media screen and (max-width: 520px) {
            align-items: flex-start;
        }
    }

    &__b-text {
        font-family: Coolvetica;
        font-weight: 400;
        font-style: Regular;
        font-size: 30px;
        leading-trim: NONE;
        line-height: 90%;
        letter-spacing: 0%;
        color: rgba(56, 56, 56, 1);
        margin: 0 0 11px;

        @include vp-767 {
            font-size: 24px;
            color: rgba(255, 255, 255, 1);
        }

        @media screen and (max-width: 520px) {
            font-size: 20px;
        }
    }

    &__info span {
        font-family: Inter;
        font-weight: 400;
        font-style: Regular;
        font-size: 20px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        color: rgba(56, 56, 56, 1);
        display: block;

        @include vp-767 {
            font-size: 18px;
            color: rgba(255, 255, 255, 1);
        }
    }

    &__info span:nth-child(3) {
        display: none;

        @media screen and (max-width: 520px) {
            display: block;
            font-size: 16px;
        }
    }

    &__info span:nth-child(2) {
       @media screen and (max-width: 520px) {
        display: none;
       }
    }

    &__btn {
        max-width: 450px;
        width: 100%;
        background: rgba(60, 43, 139, 1);
        border-radius: 60px;
        padding: 27px;
        font-family: Inter;
        font-weight: 700;
        font-style: Bold;
        font-size: 16px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        text-align: center;
        color: rgba(255, 255, 255, 1);
        transition: .3s;

        @media screen and (max-width: 950px) {
           max-width: 320px;
        }

        @include vp-767 {
            padding: 15px;
            background: rgba(255, 255, 255, 1);
            color: rgba(60, 43, 139, 1);
        }

        @media screen and (max-width: 520px) {
            max-width: 100%;
        }
    }

    &__btn:hover {
        opacity: 0.8;
    }

}