.prank {
    overflow: hidden;
    margin-top: -30px;
    
    &__wrapper {
        padding: 60px 70px 140px;
        background: rgba(185, 227, 131, 1);
        position: relative;
        overflow: hidden;

        @media screen and (max-width: 1470px) {
            padding: 60px 40px 140px;
        }

        @media screen and (max-width: 1024px) {
            padding: 60px 20px 110px;
        }

        @media screen and (max-width: 660px) {
            padding: 50px 20px 110px;
        }

        @include vp-389 {
            padding: 50px 10px 110px;
        }
    }

    &__bg {
        position: absolute;
        width: 1440px;
        height: 1240px;
        top: 150px;
        right: 0px;
        z-index: 1;

        @include vp-1279 {
            height: auto;
        }

        @include vp-767 {
            display: none;
        }
    }

    &__bg img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__info {
        position: relative;
        z-index: 1;
    }

    &__title {
        font-family: Coolvetica;
        font-weight: 400;
        font-style: Regular;
        font-size: 60px;
        leading-trim: NONE;
        line-height: 90%;
        letter-spacing: 0%;
        margin: 0 0 50px;
        color: rgba(81, 60, 176, 1);

        @include vp-767 {
            font-size: 40px;
            line-height: 100%;
            margin-bottom: 30px;
        }

        @media screen and (max-width: 520px) {
            font-size: 34px;
        }
    }

    &__list {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        gap: 20px;
        align-items: start;

        @media screen and (max-width: 1383px) {
            align-items: normal;
        }

        @include vp-1279 {
            grid-template-columns: 1fr 1fr;
        }

        @include vp-767 {
            grid-template-columns: 1fr;
        }
    }

    &__item {
        background: rgba(242, 242, 242, 1);
        border-radius: 40px;
        padding: 20px 30px 30px;
        overflow: hidden;
        display: flex;
        flex-direction: column;

        @include vp-1279 {
            padding: 20px !important;
        }

        @include vp-767 {
            grid-row: auto !important;
        }
       
    }

    &__item .prank__image {
        @include vp-1279 {
            position: absolute;
        }
    }

    &__text {
        font-family: Coolvetica;
        font-weight: 400;
        font-style: Regular;
        font-size: 40px;
        leading-trim: NONE;
        line-height: 40px;
        letter-spacing: -0.5px;
        vertical-align: middle;
        color: rgba(60, 43, 139, 1);
        margin: 0 0 20px;
    }

    &__subtext {
        font-family: Inter;
        font-weight: 400;
        font-style: Regular;
        font-size: 18px;
        leading-trim: NONE;
        line-height: 24px;
        letter-spacing: 0%;
        vertical-align: middle;
        color: rgba(60, 43, 139, 1);
        margin: 0;
        max-width: 220px;
       
        a {
            display: inline-block;
            text-decoration: underline;
            color: rgba(60, 43, 139, 1);
            font-weight: 500;
            transition: .3s;
        }

        a:hover {
            opacity: 0.8;
        }
    }

    &__item--1 {
        position: relative;
        padding-bottom: 0;
        grid-row: 1;
    }

    &__item--1 .prank__image {
        width: 300px;
        height: 400px;
        position: relative;
        right: -90px;
        margin-top: -94px;
    }

    &__item--1 .prank__image img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__item--2 {
        position: relative;
        grid-row: 1;
        padding-top: 0;
    }

    &__item--2 .prank__image {
        // width: 280px;
        // height: 300px;
        // position: relative;
        // right: -110px;
        // margin-top: -25px;
        width: 290px;
        height: 330px;
        position: relative;
        right: -100px;
        margin-top: -30px;
        top: 30px;
    }

    &__item--2 .prank__image--mobile {
        display: none;
    }

    &__item--2 .prank__info {
        @media screen and (max-width: 1383px) {
            margin-top: auto;
        }
    }

    &__item--2 .prank__image img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__item--2 .prank__subtext {
        max-width: 100%;
    }

    &__item--3 {
        background: rgba(60, 43, 139, 1);
        position: relative;
        grid-row: 1/3;

        @include vp-1279 {
            grid-row: 3;
            grid-column: 1/3;
        }
    }

    &__item--3 .prank__image {
        width: 470px;
        height: 570px;
        position: relative;
        right: 15px;
    }

    &__item--3 .prank__info {
        @media screen and (max-width: 1383px) {
            display: flex;
            flex-direction: column;
            flex-grow: 1;
        }
    }

    &__item--3 .prank__bg5 {
        display: none;
    }

    &__item--3 .prank__image img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__item--3 .prank__text {
        font-family: Coolvetica;
        font-weight: 400;
        font-style: Regular;
        font-size: 40px;
        leading-trim: NONE;
        line-height: 40px;
        letter-spacing: -0.5px;
        vertical-align: middle;
        color: rgba(242, 242, 242, 1);
        margin: -20px 0 30px;
    }

    &__item--3 .prank__subtext {
        font-family: Inter;
        font-weight: 400;
        font-style: Regular;
        font-size: 18px;
        leading-trim: NONE;
        line-height: 24px;
        letter-spacing: 0%;
        vertical-align: middle;
        color: rgba(255, 255, 255, 1);
        margin: 0 0 35px;
        max-width: 290px;
    }

    &__item--3 .prank__btn {
        background: rgba(185, 227, 131, 1);
        max-width: 100%;
        width: 100%;
        border-radius: 40px;
        padding: 18px;
        font-family: Coolvetica;
        font-weight: 400;
        font-style: Regular;
        font-size: 26px;
        leading-trim: NONE;
        line-height: 21.7px;
        letter-spacing: -0.5px;
        text-align: center;
        vertical-align: middle;
        color: rgba(60, 43, 139, 1);
        transition: .3s;

        @media screen and (max-width: 1383px) {
            margin-top: auto;
        }
    }

    &__item--3 .prank__btn:hover {
        opacity: 0.8;
    }

    &__item--4 {
        position: relative;
        grid-row: 2;
        padding-bottom: 0;
    }

    &__item--4 .prank__subtext {
        max-width: 285px;
    }

    &__item--4 .prank__image {
        position: relative;
        width: 285px;
        height: 210px;
        right: -110px;
        margin-top: auto;
    }

    &__item--4 .prank__image img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__item--4 .prank__bg1 {
        position: absolute;
        width: 70px;
        height: 110px;
        right: 0;
        top: 20px;
    }

    &__item--4 .prank__bg1 img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__item--4 .prank__bg2 {
        position: absolute;
        width: 85px;
        height: 100px;
        left: 0;
        bottom: 40px;
    }

    &__item--4 .prank__bg2 img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__item--4 .prank__bg3 {
        position: absolute;
        width: 50px;
        height: 50px;
        bottom: 0;
        left: 50px;
    }

    &__item--4 .prank__bg3 img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__item--4 .prank__bg4 {
        display: none;
    }

    &__item--5 {
        position: relative;
    }

    &__item--5 .prank__image1 {
        position: absolute;
        width: 180px;
        height: 130px;
        left: 10px;
        top: 40px;
    }

    &__item--5 .prank__image1 img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__item--5 .prank__image2 {
        position: absolute;
        width: 180px;
        height: 180px;
        right: 0;
        top: 0;
    }

    &__item--5 .prank__image2 img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__item--5 .prank__image3 {
        position: absolute;
        width: 80px;
        height: 80px;
        right: 80px;
        top: 150px;
    }

    &__item--5 .prank__image3 img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__item--5 .prank__info {
        margin-top: 228px;
    }

    &__item--5 .prank__subtext {
        max-width: 308px;
    }

    @include vp-1279 {
        &__item--1 .prank__info {
            margin-bottom: 200px;
        }

        &__item--1 .prank__text {
            max-width: 350px;
        }

        &__item--1 .prank__image {
            margin-top: 0;
            right: 0;
            bottom: 0;
            max-width: 100%;
            height: auto;
            width: 60%;
            aspect-ratio: 300/400;
        }

        &__item--2 .prank__image {
            // max-width: 100%;
            // width: 100%;
            // height: auto;
            // aspect-ratio: 130/140;
            right: 0;
            top: 25px;
            width: 250px;
            height: 250px;
        }

        &__item--4 .prank__image {
            bottom: 0;
            right: 0;
        }

        &__item--3 .prank__text {
            margin-top: 20px;
            
            br {
                display: none;
            }
        }

        &__item--3 .prank__subtext {
            max-width: 50%;
            margin-bottom: 100px;
        }

        &__item--3 .prank__image {
            top: -20px;
            right: 0;
            width: 400px;
            height: 360px;
        }
    }


    @include vp-1023 {
        &__item--1 .prank__image {
            width: 70%;
        }
    }

    @media screen and (max-width: 990px) {
        &__item--3 .prank__text {
            
            br {
                display: block;
            }
        }
    }

    @media screen and (max-width: 890px) {
        &__item--3 .prank__subtext {
            width: 40%;
        }

        &__item .prank__text {
            font-size: 30px;
            line-height: 35px;
        }
    }

    @media screen and (max-width: 820px) {
        &__item--3 .prank__image {
            width: 350px;
        }

        &__item--3 .prank__subtext {
            margin-bottom: 50px;
        }
    }

    @include vp-767 {
        &__item--3 {
            grid-column: auto;
            grid-row: 5 !important;
        }

        &__item .prank__text {
            font-size: 25px;

            br {
                display: none;
            }
        }

        &__item .prank__subtext {
            font-size: 16px;
        }

        &__item--1 .prank__info {
            margin-bottom: 0;
        }

        &__item--1 .prank__text {
            max-width: 270px;
        }

        &__item--1 .prank__subtext {
            max-width: 56%;
        }

        &__item--1 .prank__image {
            bottom: -80px;
            width: 55%;
        }

        &__item--2 .prank__subtext {
            max-width: 62%;
        }

        &__item--2 .prank__image {
            max-width: 100%;
            width: 45%;
            height: auto;
            aspect-ratio: 300/280;
            top: -110px;
        }

        &__item--4 .prank__bg2 {
            display: none;
        }

        &__item--4 .prank__bg3 {
            left: -20px;
            bottom: 100px;
        }

        &__item--4 .prank__bg1 {
            display: none;
        }

        &__item--4 .prank__bg4 {
            display: block;
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            top: 20px;
            width: 50px;
            height: 50px;
        }

        &__item--4 .prank__bg4 img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        &__item--4 .prank__image {
            max-width: 100%;
            height: auto;
            width: 42%;
            aspect-ratio: 285/210;
        }

        &__item--4 .prank__subtext {
            max-width: 60%;

            br {
                display: none;
            }
        }

        &__item--5 .prank__info {
            margin-top: 0;
        }

        .prank__item--5 .prank__image1 {
            width: 100px;
            height: 90px;
            left: 50%;
            transform: translateX(-50%);
            top: 10px;
        }

        .prank__item--5 .prank__image3 {
            top: 80px;
            right: 180px;
        }
        

    }

    @media screen and (max-width: 640px) {
        &__item--4 .prank__subtext {
            max-width: 50%;

            br {
                display: none;
            }
        }

        &__item--4 .prank__bg4 {
            left: 60%;
            top: 50px;
        }

        .prank__item--5 .prank__text {
            br {
                display: block;
            }
        }

        .prank__item--5 .prank__subtext {
            max-width: 80%;
        }

        .prank__item--5 .prank__image2 {
            
            width: 120px;
            height: 120px;
            
        }

        .prank__item--5 .prank__image3 {
            right: 0px;
            top: 105px;
        }

        &__item--3 .prank__image {
            display: none;
        }

        &__item--3 .prank__bg5 {
            display: block;
            position: relative;
            max-width: 100%;
            height: auto;
            aspect-ratio: 300/300;
            top: -20px;
            right: -20px;
        }

        &__item--3 .prank__bg5 img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        

        .prank__item--3 .prank__subtext {
            width: 100%;
            max-width: 90%;
        }

        .prank__item--3 .prank__info {
            margin-top: -90px;
        }
    }

    @media screen and (max-width: 600px) {
        &__item--2 .prank__image {
            aspect-ratio: 300/280;
            top: -20px;
        }
    }

    @media screen and (max-width: 450px) {
        &__item--1 .prank__subtext {
            max-width: 65%;
        }

        &__item--2 .prank__text {
            max-width: 63%;

            br {
                display: block;
            }
        }

        &__item--2 .prank__subtext {
            max-width: 60%;
        }

        &__item--2 .prank__image {
            display: none;
        }

        &__item--2 .prank__image--mobile {
            display: block;
            aspect-ratio: 130/140;
            top: 30px;
        }

        &__item--4 .prank__bg4 {
            left: auto;
            right: 40px;
            transform: translateX(0);
            top: 20px;
            width: 60px;
            height: 60px;
        }

        &__item--4 .prank__text {
            max-width: 55%;
        }

        &__item--4 .prank__subtext {
            max-width: 60%;
        }

        &__item--4 .prank__image {
            width: 50%;
        }

        &__item--4 .prank__bg3 {
            bottom: 120px;
        }

        .prank__item--5 .prank__image2 {
            width: 100px;
        }

        .prank__item--5 .prank__image1 {
            width: 80px;
            height: 80px;
            top: 0px;
            left: 60%;
        }

        .prank__item--3 .prank__info {
            margin-top: -70px;
        }
    }

    @include vp-389 {
        &__item .prank__subtext  {
            font-size: 14px;
        }

        &__item--2 .prank__subtext {
            max-width: 70%;
        }

        &__item--3 .prank__text {
            margin-bottom: 20px;
            br {
                display: block;
            }
        }
    }
}