.chance {
    position: relative;
    overflow: hidden;
    margin-top: -50px;
    &__wrapper {
        padding: 100px 70px 150px;
        background: rgba(116, 200, 8, 1);
        position: relative;
        overflow: hidden;

        @media screen and (max-width: 1470px) {
            padding: 100px 40px 150px;
        }
    
        @media screen and (max-width: 1024px) {
            padding: 100px 20px 100px;
        }

        @include vp-389 {
            padding: 90px 10px 100px;
        }
    }

    &__bg {
        position: absolute;
        width: 880px;
        height: 530px;
        left: 50%;
        top: 270px;
        transform: translateX(-50%);

        @media screen and (max-width: 1200px) {
            width: 750px;
            height: 450px;
        }

        @media screen and (max-width: 950px) {
            width: 550px;
            height: 330px;
        }
        
    }

    &__bg img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__title {
        font-family: Coolvetica;
        font-weight: 400;
        font-style: Regular;
        font-size: 60px;
        leading-trim: NONE;
        line-height: 90%;
        letter-spacing: 0%;
        text-align: center;
        color: rgba(255, 255, 255, 1);
        margin: 0 0 100px;

        span {
            color: rgba(81, 60, 176, 1);
        }

        &--mobile {
            display: none;
        }

        @include vp-767 {
            font-size: 40px;
            text-align: left;
            margin-bottom: 20px;
        }
    }


    &__box {
        display: flex;
        justify-content: space-between;
        position: relative;
    }

    &__buttons {
        display: none;

        @include vp-767 {
            display: flex;
            position: static;
            gap: 10px;
            margin-bottom: 30px;
        }

        .swiper-button-prev,
        .swiper-button-next {
            @include vp-767 {
                display: flex !important;
                position: static;
                height: auto;
                width: auto;
            }
        }
    }



    .swiper-button-prev,
    .swiper-button-next {
        top: 200px;
        margin-top: 0;
        height: auto;
        svg {
            width: 50px;
            height: 50px;
            flex-shrink: 0;

            @include vp-767 {
                width: 40px;
                height: 40px;
            }
        }

        @media screen and (max-width: 950px) {
            top: 100px;
        }

        @include vp-767 {
            display: none;
        }
    }

    .swiper-button-prev {
        left: 10px;

        @media screen and (max-width: 1330px) {
            left: -20px;
        }

        @include vp-1023 {
            left: 0;
        }

        
        @media screen and (max-width: 950px) {
            left: calc(50% - 345px); 
        }
    }

    .swiper-button-next {
        right: 10px;

        @media screen and (max-width: 1330px) {
            right: -20px;
        }

        @include vp-1023 {
            right: 0;
        }

        @media screen and (max-width: 950px) {
            right: calc(50% - 345px);
        }
    }

    .swiper-button-prev::after,
    .swiper-button-next::after {
        display: none;
    }

    .swiper-button-prev svg {
        transform: rotate(-180deg);
    }

    &__slider.swiper {
        max-width: 1000px;
        width: 100%;
        height: 100%; 
        margin-bottom: 65px;
        
        @media screen and (max-width: 1200px) {
            max-width: 770px;
        }

        @media screen and (max-width: 950px) {
            max-width: 580px;
        }
    }

    .swiper-wrapper {
        height: auto;
    }
      
    .swiper-slide {
        max-width: 100%; 
        width: 370px !important;  
        height: auto;
        aspect-ratio: 370/330;
        transition: all 0.4s ease;

        @media screen and (max-width: 1200px) {
            max-width: 310px !important;
            aspect-ratio: 300/330;
        }

        @media screen and (max-width: 950px) {
            max-width: 230px !important;
        }
    }

    .swiper-slide img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
      
    .swiper-slide-active {
        max-width: 400px !important;
        height: 480px !important;
        width: 100%;
        z-index: 5;
        transform: scale(1.05);

        @media screen and (max-width: 1200px) {
            max-width: 310px !important;
            height: 400px !important;
        }

        @media screen and (max-width: 950px) {
            max-width: 250px !important;
            height: 300px !important;
            transform: scale(1.1);
        }
    }

    &__logo-box {
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 22px;

        @media screen and (max-width: 950px) {
            gap: 10px;
        }
    }

    &__logo {
        max-width: 100%;
        width: 100px;
        height: auto;
        aspect-ratio: 100/100;
        border-radius: 20px;
        overflow: hidden;

        @media screen and (max-width: 950px) {
            width: 80px;
        }
    }

    &__logo img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    @media screen and (max-width: 620px) {


        &__bg {
            top: 375px;
        }

        &__slider.swiper {
            max-width: 75%;
            width: 100%;
        }


        .swiper-slide,
        .swiper-slide-active {
            width: 100% !important;
            max-width: 100% !important;
            height: auto !important;
            transform: none !important;
            aspect-ratio: 200/240;
        }
        
    }

    @media screen and (max-width: 520px) {

        &__bg {
            width: 450px;
            height: 270px;
            top: 400px;
        }


        &__title {
            display: none;

            &--mobile {
                display: block;
                font-size: 30px;
            }
        }

        &__slider.swiper {
            max-width: 80%;
        }
    }

    @media screen and (max-width: 440px) {

        &__bg {
            width: 365px;
            height: 220px;
            top: 400px;
        }


        &__logo-box {
            flex-wrap: wrap;
        }

        &__logo {
            max-width: none;
            height: 60px;
            width: 60px;
        }
    }

    @include vp-389 {
        &__bg {
            width: 300px;
            height: 180px;
            top: 380px;
        }
    }
      
}