.footer {
  margin-top: -70px;
  overflow: hidden;
  margin-bottom: 55px;
  &__wrapper {
    background: rgba(39, 31, 89, 1);
    display: flex;
    justify-content: space-between;
    gap: 20px;
    position: relative;
    padding: 65px 70px 35px;
    border-radius: 63px;
    overflow: hidden;
    @media screen and (max-width: 1470px) {
      padding: 65px 40px 35px;
    }

    @include vp-1279 {
      border-radius: 20px;
    }

    @media screen and (max-width: 1024px) {
      padding: 50px 20px 30px;
    }

    @media screen and (max-width: 920px) {
      flex-direction: column;
      align-items: center;
      gap: 30px;
    } 

    @include vp-389 {
      padding: 50px 10px 30px;
    }
  }

  &__left {
    display: flex;
    flex-direction: column;
  }

  &__logo {
    width: 270px;
    height: 35px;
    margin-bottom: 73px;

    @media screen and (max-width: 920px) {
      margin-bottom: 0;
    }
  }

  &__logo img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__policy {
    max-width: 260px;
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 15px;


    @media screen and (max-width: 920px) {
      display: none;
    }
    &--mobile {
      display: none;

      @media screen and (max-width: 920px) {
        display: flex;
        margin-top: 20px;
        max-width: 100%;
      }
    }
  }

  &__link {
    font-family: Inter;
    font-weight: 400;
    font-style: Regular;
    font-size: 15px;
    leading-trim: NONE;
    line-height: 130%;
    letter-spacing: 0%;
    text-decoration: underline;
    text-decoration-style: solid;
    text-decoration-offset: 0%;
    text-decoration-thickness: 0%;
    text-decoration-skip-ink: auto;
    color: rgba(255, 255, 255, 1);
    transition: .3s;

    @media screen and (max-width: 920px) {
      text-align: center;
    }
  }

  &__link:hover {
    opacity: 0.8;
  }

  &__center {
    display: flex;
    flex-direction: column;
    gap: 10px;
    max-width: 390px;
    width: 100%;
  }

  &__circs {
    max-width: 390px;
    width: 100%;
    background: rgba(179, 225, 0, 1);
    border-radius: 60px;
    padding: 17px;
    font-family: Inter;
    font-weight: 700;
    font-style: Bold;
    font-size: 16px;
    leading-trim: NONE;
    line-height: 100%;
    letter-spacing: 0%;
    text-align: center;
    color: rgba(56, 56, 56, 1);
    transition: .3s;

    @media screen and (max-width: 520px) {
      background: rgba(185, 227, 131, 1);
      color: rgba(60, 43, 139, 1);
      font-size: 16px;
    }
  }

  &__circs:hover {
    opacity: 0.8;
  }

  &__winer {
    max-width: 390px;
    width: 100%;
    background: rgba(255, 255, 255, 1);
    border-radius: 60px;
    padding: 17px;
    font-family: Inter;
    font-weight: 700;
    font-style: Bold;
    font-size: 16px;
    leading-trim: NONE;
    line-height: 100%;
    letter-spacing: 0%;
    text-align: center;
    color: rgba(56, 56, 56, 1);
    transition: .3s;

    @media screen and (max-width: 520px) {
      
      color: rgba(60, 43, 139, 1);
      font-size: 16px;
    }
  }

  &__winer:hover {
    opacity: 0.8;
  }

  &__right {
    display: flex;
    flex-direction: column;

    @media screen and (max-width: 920px) {
      flex-direction: column-reverse;
      gap: 30px;
    }
  }

  &__low {
    font-family: Inter;
    font-weight: 400;
    font-style: Regular;
    font-size: 15px;
    leading-trim: NONE;
    line-height: 130%;
    letter-spacing: 0%;
    color: rgba(255, 255, 255, 1);
    margin: 0;
  }

  &__we {
    margin-top: auto;
  }

  &__we svg {
    width: 230px;
    height: 30px;
    flex-shrink: 0;
    transition: .3s;
  }

  &__we:hover svg {
    opacity: 0.8;
  }

}






.modals {
  .cookie-modal {
    
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 30px;
    z-index: -1;
    max-width: 405px;
    width: 100%;
    opacity: 0;           
    overflow: hidden;     
    transition: .3s;


    &.is-visible {
      opacity: 1;
      overflow: visible;  
      transition: .3s;
      z-index: 10;
    }

    @media screen and (max-width: 520px) {
      max-width: 320px;
    }
  }


  .cookie__content {
    padding: 12px;
    border: 1px solid rgba(179, 225, 0, 1);
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 10px;
    background: rgba(255, 255, 255, 1);
    border-radius: 8px;
  }

  .cookie__text {
    font-family: Inter;
    font-weight: 600;
    font-style: Semi Bold;
    font-size: 13px;
    leading-trim: NONE;
    line-height: 100%;
    letter-spacing: 0%;
    color: rgba(56, 56, 56, 1);
    margin: 0;
    max-width: 275px;
    width: 100%;
  }

  .cookie__btn {
    max-width: 70px;
    width: 100%;
    background: rgba(179, 225, 0, 1);
    border-radius: 32px;
    padding: 8px;
    font-family: Coolvetica;
    font-weight: 400;
    font-style: Regular;
    font-size: 19px;
    leading-trim: NONE;
    line-height: 100%;
    letter-spacing: 0%;
    color: rgba(56, 56, 56, 1);
    transition: .3s;
  }

  .cookie__btn:hover {
    opacity: 0.8;
  }

  // Регистрация кода //

  .reg-modal {
    position: fixed;
    inset: 0;
    z-index: 100; 
    opacity: 0;
    visibility: hidden;
    transition: .3s;

    &.is-active {
      opacity: 1;
      visibility: visible;
    }

    .reg-modal__overlay {
      position: absolute;
      inset: 0;
      background: rgba(0, 0, 0, 0.6);
      cursor: pointer;
    }

    .reg-modal__content {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      max-width: 440px;
      width: 100%;

      @media (max-width: 520px) {
        max-width: 100%;
        padding: 10px;
      }
    }

    .reg-modal__box {
      position: relative;
      background: #fff;
      border-radius: 30px;
      padding: 15px;
      width: 100%;
      z-index: 2;
      display: flex;
      flex-direction: column;
  
      
    }

    .reg-modal__close {
      position: absolute;
      right: 15px;
      top: 15px;
    }

    .reg-modal__text {
      font-family: Coolvetica;
      font-weight: 400;
      font-style: Regular;
      font-size: 24px;
      leading-trim: NONE;
      line-height: 100%;
      letter-spacing: 0%;
      color: rgba(56, 56, 56, 1);
      margin: 20px 0 15px;
    }

    .reg-modal__form {
      display: flex;
      flex-direction: column;
      gap: 10px;
    }

    .custom-input input {
      padding: 20px;
      background: rgba(248, 248, 248, 1);
      border-radius: 40px;
      color:rgba(56, 56, 56, 1);
    }

    .custom-input input::placeholder {
      font-family: Inter;
      font-weight: 400;
      font-style: Regular;
      font-size: 15px;
      leading-trim: NONE;
      line-height: 100%;
      letter-spacing: 0%;
      color: rgba(157, 153, 153, 1);
    }

    .custom-checkbox__text {
      font-family: Inter;
      font-weight: 400;
      font-style: Regular;
      font-size: 10px;
      leading-trim: NONE;
      line-height: 100%;
      letter-spacing: 0%;
      color: rgba(56, 56, 56, 1);
    }

    .custom-checkbox__icon {
      width: 10px;
      height: 10px;
      border-radius: 0;
      border: 1px solid rgba(56, 56, 56, 1);
    }

    .reg-modal__btn {
      background: rgba(179, 225, 0, 1);
      max-width: 100%;
      padding: 22px;
      border-radius: 60px;
      font-family: Inter;
      font-weight: 700;
      font-style: Bold;
      font-size: 16px;
      leading-trim: NONE;
      line-height: 100%;
      letter-spacing: 0%;
      text-align: center;
      color: rgba(56, 56, 56, 1);
      transition: .3s;
    }

    .reg-modal__btn:hover {
      opacity: 0.8;
    }

    .reg-modal__small {
      font-family: Inter;
      font-weight: 400;
      font-style: Regular;
      font-size: 13px;
      leading-trim: NONE;
      line-height: 100%;
      letter-spacing: 0%;
      color: rgba(56, 56, 56, 1);
    }

    .reg-modal__small a {
      display: inline;
      text-decoration: underline;
      transition: .3s;
    } 

    .reg-modal__small a:hover {
      opacity: 0.8;
    }
  }

