.intro {
    position: relative;
    overflow: hidden;
    .intro__bg-left {
        position: absolute;
        width: 300px;
        height: 330px;
        top: 310px;
        left: 0;
        z-index: 1;

        @media screen and (max-width: 1150px) {
            display: none;
        }
    } 

    .intro__bg-left img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    .intro__bg-right {
        position: absolute;
        width: 240px;
        height: 360px;
        top: 125px;
        right: 0;
        z-index: 1;

        @media screen and (max-width: 1150px) {
            display: none;
        }
    }

    .intro__bg-right img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }


    &__wrapper {
        background-color: rgba(116, 200, 8, 1);
        padding: 170px 70px 0;
        position: relative;

        @media screen and (max-width: 1470px) {
            padding: 170px 40px 0;
        }

        @media screen and (max-width: 1024px) {
            padding: 170px 20px 0;
        }

        @media screen and (max-width: 660px) {
            padding: 130px 20px 0;
        }

        @include vp-389 {
            padding: 125px 10px 0;
        }
    }

    &__title {
        position: relative;
        font-family: Coolvetica;
        font-weight: 400;
        font-style: Regular;
        font-size: 130px;
        leading-trim: NONE;
        line-height: 120%;
        letter-spacing: 0%;
        text-align: center;
        color: rgba(242, 242, 242, 1);
        margin: 0 0 15px;
        z-index: 1;

        @media screen and (max-width: 1170px) {
            font-size: 100px;
        }

        @media screen and (max-width: 850px) {
            font-size: 80px;
        }

        @media screen and (max-width: 660px) {
            font-size: 60px;
        }

        @media screen and (max-width: 520px) {
            font-size: 50px;
            line-height: 110%;
        }
    }

    .intro__title-bg {
        width: 140px;
        height: 140px;
        position: absolute;
        left: 40%;
        top: -30px;
        z-index: -1;

        @include vp-1023 {
            width: 110px;
            height: 110px;
        }

        @media screen and (max-width: 660px) {
            width: 60px;
            height: 60px;
            top: -10px;
        }

        @media screen and (max-width: 430px) {
            top: -20px;
            left: 56%;
        }
    }

    &__block {
        position: relative;
    }

    &__small-text {
        position: absolute;
        font-family: Inter;
        font-weight: 700;
        font-style: Bold;
        font-size: 18px;
        leading-trim: NONE;
        line-height: 120%;
        letter-spacing: 0%;
        color: rgba(60, 43, 139, 1);
        background: rgba(185, 227, 131, 1);
        border-radius: 25px;
        padding: 20px 20px 20px 20px;
        max-width: 205px;
        margin: 0;
        top: 240px;
        
        left: 41%; 
        transform: translateX(-500px);
        display: flex;
        flex-direction: row-reverse;
        justify-content: start;
        gap: 10px;
        z-index: 1;

        

        @media screen and (max-width: 1410px) {
            left: 47%;
        }

        
        @media screen and (max-width: 1410px) {
            top: 100px;
            left: 57%;
        }

        @include vp-1023 {
            width: 100%;
            top: 30px;
            left: 50%;
            transform: translateX(-50%);
        }

        @include vp-767 {
            background: rgba(255, 255, 255, 0.8);
            top: -15px;
        }

        @media screen and (max-width: 520px) {
            border-radius: 15px;
            font-size: 16px;
            padding: 12px;
            max-width: 170px;
        }
    }

    &__small-text--2 {
        position: absolute;
        top: 120px;
        right: 35%; 
        left: auto;             
        transform: translateX(500px); 
        max-width: 344px;
        p {
            font-family: Inter;
            font-weight: 700;
            font-style: Bold;
            font-size: 18px;
            leading-trim: NONE;
            line-height: 120%;
            letter-spacing: 0%;
            margin: 0;

            @media screen and (max-width: 520px) {
                font-size: 16px;
            }
        }

        span {
            font-family: Inter;
            font-weight: 400;
            font-style: Regular;
            font-size: 16px;
            leading-trim: NONE;
            line-height: 100%;
            letter-spacing: 0%;
            color: rgba(60, 43, 139, 0.6);
            display: block;
            margin-top: 10px;

            @include vp-767 {
                color: rgba(60, 43, 139, 0.8);
            }

            @media screen and (max-width: 520px) {
                font-size: 14px;
            }
        }

        @media screen and (max-width: 1480px) {
            max-width: 305px;
            left: auto;
            right: 38%;
        }

        @media screen and (max-width: 1410px) {
            left: auto;
            right: 43%;
            top: 100px;
            max-width: 305px;
        }


           

        @media screen and (max-width: 1280px) {
            left: auto;
            right: 48%;
            top: 25px;
        }

        
        @media screen and (max-width: 1150px) {
            left: auto;
            right: 54%;
            top: -30px;
        }

        @include vp-1023 {
            width: 100%;
            max-width: 340px;
            top: 150px;
            left: 50%;
            transform: translateX(-50%);
        }

        
        @include vp-767 {
            background: rgba(255, 255, 255, 0.8);
            top: 90px;

        }

        @media screen and (max-width: 520px) {
            top: 70px;
            max-width: 300px;
        }

        @include vp-389 {
            max-width: 100%;
        }
    }

    &__small-text2 {
        @include vp-389 {
            max-width: 85%;
        }
    }

    &__small-text svg {
        width: 12px;
        height: 22px;
        flex-shrink: 0;
    }

    .intro__title-bg img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__text {
        font-family: Inter;
        font-weight: 400;
        font-style: Regular;
        font-size: 24px;
        leading-trim: NONE;
        line-height: 80%;
        letter-spacing: 0%;
        text-align: center;
        vertical-align: middle;
        color: rgba(60, 43, 139, 1);
        margin: 0 0 25px;

        @include vp-767 {
            font-size: 20px;
        }

        @media screen and (max-width: 520px) {
            font-size: 18px;
        }
    }

    &__btn {
        margin: 0 auto;
        display: flex;
        justify-content: center;
        font-family: Coolvetica;
        font-weight: 400;
        font-style: Regular;
        font-size: 30px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0.5px;
        text-align: center;
        vertical-align: middle;
        max-width: 300px;
        margin-bottom: 30px;

        @include vp-767 {
            font-size: 24px;
            margin-bottom: 50px;
            max-width: 300px;
        }

        @media screen and (max-width: 520px) {
            font-size: 20px;
            padding: 11px;
            max-width: 200px;
        }
    }

    &__big-bg {
        position: relative;
        z-index: 2;
        width: 1000px;
        height: 500px;
        margin: 0 auto;


        @media screen and (max-width: 1410px) {
            width: 750px;
            height: 415px;
        }

        
        @media screen and (max-width: 1150px) {
            width: 520px;
            height: 215px;
        }

        @include vp-1023 {
            display: none;
        }
    }

    &__big-bg img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__bg-mobile {
        display: none;

        @include vp-1023 {
            display: block;
            position: relative;
            margin-bottom: -160px;
            max-width: 100%;
            height: auto;
            aspect-ratio: 325/280;
        }

        @media screen and (max-width: 520px) {
            margin-bottom: -80px;
        }

        @media screen and (max-width: 360px) {
            margin-bottom: -20px;
        }
    }

    &__bg-mobile img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__bg-small1 {
        width: 100px;
        height: 100px;
        position: absolute;
        left: 300px;
        top: 400px;

        @media screen and (max-width: 1150px) {
            left: 50px;
            top: 330px;
        }

        @include vp-767 {
            left: -30px;
            top: 300px;
        }

        @media screen and (max-width: 660px) {
            top: 240px;
        }

        
        @media screen and (max-width: 520px) {
            width: 80px;
            height: 80px;
            left: -20px;
            top: 210px;
        }

        @media screen and (max-width: 430px) {
            top: 280px;
        }
    }

    &__bg-small1 img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__bg-small4 {
        position: absolute;
        width: 190px;
        height: 190px;
        right: -30px;
        bottom: 0;

        @media screen and (max-width: 1150px) {
            width: 130px;
            height: 130px;
        }

        @include vp-1023 {
            display: none;
        }

        &--mobile {
            display: none;
            @include vp-1023 {
                display: block;
                bottom: auto;
                top: -50px;
                right: -60px;
            }

            @media screen and (max-width: 660px) {
                top: -80px;
            }

            @media screen and (max-width: 520px) {
                width: 100px;
                height: 100px;
                right: -45px;
            }
        }
    }

    &__bg-small4 img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}