.wash {
    position: relative;
    overflow: hidden;
    margin-top: -50px;
    z-index: 1;
    &__wrapper {
        background: rgba(116, 200, 8, 1);
        padding: 70px 70px 85px;
        border-radius: 60px 60px 0 0;
        position: relative;
        @media screen and (max-width: 1470px) {
            padding: 70px 40px 85px;
        }
    
        @media screen and (max-width: 1024px) {
            padding: 40px 20px 70px;
            border-radius: 32px 32px 0 0;
        }

        @include vp-389 {
            padding: 40px 10px 70px;
        }

        &::before {
            content: '';
            position: absolute;
            height: 600px;
            width: 360px;
            background: white;
            left: -360px;
            top: 20%;
            z-index: 10;
        }
    
    }

    &__title {
        font-family: Coolvetica;
        font-weight: 400;
        font-style: Regular;
        font-size: 60px;
        leading-trim: NONE;
        line-height: 90%;
        letter-spacing: 0%;
        color: rgba(255, 255, 255, 1);
        margin: 0 0 30px;

        span {
            color: rgba(81, 60, 176, 1);
        }

        @include vp-767 {
            font-size: 40px;
            margin-bottom: 20px;
        }

        @media screen and (max-width: 520px) {
            font-size: 30px;
        }
    }

    &__top {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 65px;

        @include vp-767 {
            margin-bottom: 30px;
        }

        @media screen and (max-width: 520px) {
            flex-direction: column;
            align-items: flex-start;
            gap: 30px;
        }
    }

    &__text {
        font-family: Inter;
        font-weight: 700;
        font-style: Bold;
        font-size: 21px;
        leading-trim: NONE;
        line-height: 110%;
        letter-spacing: 0%;
        color: rgba(255, 255, 255, 1);
        margin: 0;
        max-width: 450px;

        @include vp-767 {
            font-size: 18px;
            max-width: 400px;
        }

        @media screen and (max-width: 520px) {
            font-size: 16px;
            max-width: 350px;
        }

        @include vp-389 {
            max-width: 85%;
        }
    }

    &__buttons {
        position: static;
        display: flex;
        gap: 6px;
        max-width: 130px;
    }

    .swiper-button-prev,
    .swiper-button-next {
        position: static;
        width: auto;
        margin-top: 0;
        height: auto;
        svg {
            width: 50px;
            height: 50px;
            flex-shrink: 0;

            @include vp-767 {
                width: 40px;
                height: 40px;
            }
        }
    }

    .swiper-button-prev::after,
    .swiper-button-next::after {
        display: none;
    }

    .swiper-button-prev svg {
        transform: rotate(-180deg);
    }

    &__slider {
        overflow-x: visible;
    }

    .swiper-wrapper {
        height: auto;
    }

    .swiper-slide {
        max-width: 360px;
        height: 360px;
        width: 100%;
        background: rgba(242, 242, 242, 1);
        padding: 30px 30px 25px 30px;
        border-radius: 40px;
        overflow: hidden;
        position: relative;
        display: flex;
        flex-direction: column;

        @media screen and (max-width: 520px) {
            padding: 15px 20px 20px;
            border-radius: 20px;
        }
    }

    &__slide-text {
        font-family: Coolvetica;
        font-weight: 400;
        font-style: Regular;
        font-size: 26px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        color: rgba(60, 43, 139, 1);
        margin: 0;

        br {
            display: none;

            @media screen and (max-width: 390px) {
                display: block;
            }
        }
        @include vp-767 {
            font-size: 24px;
        }

        @media screen and (max-width: 520px) {
            font-size: 22px;
        }
    }

    &__image {
        width: 100%;
        height: auto;
        position: absolute;
        bottom: 0;
        right: 0;
        left: 0;
        z-index: -1;
    }

    &__image img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    .swiper-slide span {
        background: rgba(255, 255, 255, 1);
        border-radius: 30px;
        padding: 12px;
        max-width: 150px;
        width: 100%;
        display: flex;
        flex-direction: row-reverse;
        align-items: center;
        justify-content: start;
        gap: 15px;
        margin-top: auto;
        align-self: flex-end;
        font-family: Coolvetica;
        font-weight: 400;
        font-style: Regular;
        font-size: 18px;
        leading-trim: NONE;
        line-height: 16px;
        letter-spacing: 0.5px;
        text-align: center;
        vertical-align: middle;
        color: rgba(60, 43, 139, 1);
        svg {
            width: 12px;
            height: 12px;
           
            circle {
                fill: rgba(60, 43, 139, 1);
            }
            
        }
    }

    .swiper-slide:nth-child(3) span {
        background: rgba(60, 43, 139, 1);
        color: rgba(255, 255, 255, 1);

        svg {
            circle {
                fill: rgba(255, 255, 255, 1);
            }
            
        }
    }
}