.custom-checkbox__icon {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  width: 20px;
  height: 20px;
  margin-right: 8px;
  border-radius: 4px;
  border: 2px solid #ccc;
  background-color: #fff;
  transition: all 0.3s ease;
}
.custom-checkbox__icon img, .custom-checkbox__icon svg {
  width: 12px;
  height: 12px;
  opacity: 0;
  transform: scale(0.6);
  transition: all 0.25s ease;
}
.custom-checkbox input {
  position: absolute;
  opacity: 0;
  pointer-events: none;
}
.custom-checkbox input:checked + .custom-checkbox__icon {
  background-color: #b3e100;
  border-color: #b3e100;
}
.custom-checkbox input:checked + .custom-checkbox__icon img, .custom-checkbox input:checked + .custom-checkbox__icon svg {
  opacity: 1;
  transform: scale(1);
}
