.header {
  z-index: 100;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 10px 0 0;
  // overflow: hidden;
  &::before {
    content: "";
    position: absolute;
    width: 100%;
    top: 0;
    left: 0;
    background: white;
    height: 109px;

    @include vp-1023 {
      height: 80px;
    }
  
  }
  
  &__wrapper {
    background-color: rgba(116, 200, 8, 1);
    border-radius: 64px 64px 0 0;
    padding: 25px 70px;
    position: relative;
    z-index: 3;

    @media screen and (max-width: 1470px) {
      padding: 25px 40px;
    }

    
    @include vp-1023 {
      border-radius: 32px 32px 0 0;
      padding: 15px 20px;
    }
  }



  &__nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    gap: 20px;
  }

  
  .header__logo {
    width: 260px;
    height: 50px;
    flex-shrink: 0;

    @media screen and (max-width: 1280px) {
      width: 240px;
    }

    @media screen and (max-width: 520px) {
      width: 200px;
      height: 35px;
    }

    @include vp-389 {
      width: 170px;
      height: 35px;
    }
  }


  

  &__center {
   
    
    transition: .3s;

    @media screen and (max-width: 1280px) {
      width: 100%;
    }


    @media screen and (max-width: 1150px) {
      transform: translateX(110%);
      position: fixed;
      z-index: 10;
      overflow-y: auto;
      overflow-x: hidden;
      top: 90px;
      left: 0;
      right: 0;
      bottom: 0;
      background: rgba(185, 227, 131, 1);
      padding: 30px 20px;
      margin: 0 40px;
      width: auto;
    }

    @include vp-1023 {
      margin: 0 10px;
      top: 80px;
    }

    @include vp-767 {
      margin: 0;
    }
  }

  &.is-open .header__center {
    transform: translateX(0%);
  }

  .main-nav__list {
    display: flex;
    align-items: center;
    gap: 30px;
    justify-content: center;
    @media screen and (max-width: 1280px) {
      gap: 20px;
      
    }

    @include vp-767 {
      margin-bottom: 30px;
    }
  }

  &.is-open .main-nav__list {
    justify-content: start;
    flex-direction: column;
    align-items: flex-start;
  }

  .main-nav__link {
    font-family: Inter;
    font-weight: 500;
    font-style: Medium;
    font-size: 15px;
    leading-trim: NONE;
    line-height: 100%;
    letter-spacing: 0%;
    color: rgba(242, 242, 242, 1);
    display: block;
    transition: .3s;
    &:hover {
      opacity: 0.6;
    }
  }

  &.is-open .main-nav__link {
    color: rgba(60, 43, 139, 1);
    text-transform: uppercase;
  }


  .main-nav__toggle {
    position: relative;
    display: none;
    width: 35px;
    height: 40px;
    padding: 0;
    cursor: pointer;
    flex-shrink: 0;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    @media screen and (max-width: 1150px) {
      display: flex;
      margin-left: auto;
    }

    @include vp-389 {
      height: 35px;
      width: 30px;
    }
  }
  
  .main-nav__toggle span {
    display: block;
    position: absolute;
    left: 0px;
    height: 5px;
    width: 35px;
    border-radius: 100px;
    background-color: rgba(255, 255, 255, 1);
    -webkit-transition: opacity 0.3s ease, visibility 0.3s ease, -webkit-transform 0.3s ease;
    -o-transition: transform 0.3s ease, opacity 0.3s ease, visibility 0.3s ease;
    transition: transform 0.3s ease, opacity 0.3s ease, visibility 0.3s ease;
    transition: transform 0.3s ease, opacity 0.3s ease, visibility 0.3s ease, -webkit-transform 0.3s ease;

    @include vp-389 {
      height: 3px;
      width: 30px;
    }
  }
  
  .main-nav__toggle span:nth-child(1) {
    top: 8px;
  }
  
  .main-nav__toggle span:nth-child(2) {
    top: 50%;
  
    -webkit-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
  }
  
  .main-nav__toggle span:nth-child(3) {
    bottom: 8px;
  }
  
  .main-nav__toggle.focus-visible:focus {
    opacity: 0.8;
  }

  &.is-open .main-nav__toggle span:nth-child(1) {
    top: 12px;
    
    transform: rotate(-45deg) translate(0px, 5px);
    background: rgba(255, 255, 255, 1);
    height: 5px;
    width: 35px;
  }
  
  &.is-open .main-nav__toggle span:nth-child(2) {
    opacity: 0;
    visibility: hidden;
  }
  
  &.is-open .main-nav__toggle span:nth-child(3) {
    top: 20px;
    -webkit-transform: rotate(45deg) translate(0, 50%);
    -ms-transform: rotate(45deg) translate(0, 50%);
    transform: rotate(45deg) translate(0px, -7px);
    background: rgba(255, 255, 255, 1);
    height: 5px;
    width: 35px;
  }

  &__bottom {
    display: flex;
    align-items: center;
    gap: 30px;
    max-width: 300px;
    width: 100%;

    @media screen and (max-width: 1280px) {
      max-width: 280px;
      gap: 20px;
    }

    @media screen and (max-width: 1150px) {
      width: auto;
      max-width: fit-content;
      align-items: flex-start;
      gap: 15px;
    }
  }

  &__btn-reg {
    max-width: 230px;
    width: 100%;
    background: rgba(60, 43, 139, 1);
    border-radius: 60px;
    padding: 13px;
    font-family: Coolvetica;
    font-weight: 300;
    font-style: Regular;
    font-size: 18px;
    leading-trim: NONE;
    line-height: 18.6px;
    letter-spacing: 0.5px;
    text-align: center;
    vertical-align: middle;
    color: rgba(242, 242, 242, 1);
    transition: .3s;

    @include vp-767 {
      max-width: 100%;
    }
  }

  &__btn-reg:hover {
    opacity: 0.8;
  }

  &__btn-log svg{
    width: 40px;
    height: 40px;
    flex-shrink: 0;
    transition: .3s;

    @include vp-389 {
      width: 32px;
      height: 35px;
    }
  }

  &__btn-log:hover svg {
    opacity: 0.8;
  }

}