.drum {
    position: relative;
    overflow: hidden;

    &__wrapper {
        position: relative;
        padding: 70px 70px 80px;
        overflow: hidden;
        @media screen and (max-width: 1470px) {
            padding: 70px 40px 80px;
        }
    
        @media screen and (max-width: 1024px) {
            padding: 70px 20px;
        }

        @include vp-389 {
            padding: 70px 10px;
        }
    }

    &__bg {
        position: absolute;
        width: 1300px;
        height: 510px;
        left: 49%;
        transform: translateX(-50%);
        bottom: -90px;
        z-index: -1;

        @media screen and (max-width: 520px) {
            bottom: -10px;
        }

        @include vp-389 {
            display: none;
            
        }
    }

    &__bg-mobile {
        display: none;

        @include vp-389 {
            display: block;
            position: absolute;
            width: 430px;
            height: 710px;
            bottom: -280px;
            left: 50%;
            transform: translateX(-50%);
            z-index: -1;
        }

        @media screen and (max-width: 340px) {
            bottom: -300px;
        }
    }

    &__bg img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__small1 {
        position: absolute;
        width: 130px;
        height: 105px;
        left: 110px;
        top: 120px;

        @include vp-767 {
            left: 0;
            top: 250px;
        }

        @media screen and (max-width: 520px) {
            width: 100px;
            height: 100px;
            top: 200px;
        }

        @include vp-389 {
            width: 80px;
            height: 80px;
        }

        @media screen and (max-width: 340px) {
            top: 250px;
            left: -10px;
        }
    }

    &__small1 img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__small2 {
        position: absolute;
        width: 220px;
        height: 230px;
        left: 0;
        top: 160px;

        @include vp-1023 {
            width: 150px;
            height: 150px;
            top: 200px;
        }

        @include vp-767 {
            top: 500px;
        }

        @media screen and (max-width: 520px) {
            width: 100px;
            height: 100px;
            top: 430px;
        }

        @include vp-389 {
            width: 90px;
            height: 90px;
            top: 385px;
        }

        @media screen and (max-width: 340px) {
            top: 400px;
        }
    }

    &__small2 img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__small3 {
        position: absolute;
        width: 220px;
        height: 260px;
        right: 0;
        top: 80px;

        @include vp-1023 {
            width: 150px;
            height: 200px;
        }

        @include vp-767 {
            top: 350px;
            width: 100px;
            height: 120px;
        }

        @media screen and (max-width: 520px) {
            top: 250px;
            width: 85px;
            height: 100px;
        }

        @include vp-389 {
            top: 230px;
            width: 70px;
            height: 80px;
        }

        @media screen and (max-width: 340px) {
            top: 280px;
        }
    }

    &__small3 img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__small4 {
        position: absolute;
        width: 170px;
        height: 170px;
        right: 150px;
        bottom: 250px;

        @include vp-1279 {
            right: 0;
        }

        @include vp-767 {
            width: 140px;
            height: 140px;
            right: -25px;
            bottom: 300px;
        }

        @media screen and (max-width: 520px) {
            width: 100px;
            height: 100px;
            right: -25px;
            bottom: 400px;
        }

        @include vp-389 {
            width: 120px;
            height: 120px;
            right: -25px;
            bottom: 280px;
        }

        @media screen and (max-width: 340px) {
            bottom: 290px;
        }

    }

    &__small4 img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__info {
        display: flex;
        flex-direction: column;
        align-items: center;
        position: relative;
        z-index: 1;
    }

    &__title {
        font-family: Coolvetica;
        font-weight: 400;
        font-style: Regular;
        font-size: 90px;
        leading-trim: NONE;
        line-height: 90%;
        letter-spacing: 0%;
        text-align: center;
        color: rgba(107, 82, 221, 1);
        margin: 0 0 30px;

        @include vp-1023 {
            font-size: 70px;
        }

        @media screen and (max-width: 520px) {
            font-size: 50px;
        }
    }

    &__text {
        display: block;
        
        text-align: center;
        background: rgba(107, 82, 221, 1);
        border-radius: 100px;
        padding: 5px 10px 10px;
        font-family: Coolvetica;
        font-weight: 400;
        font-style: Regular;
        font-size: 30px;
        leading-trim: NONE;
        line-height: 80%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: rgba(255, 255, 255, 1);
        transform: rotate(1.37deg);
        margin: 0 0 77px;
        @include vp-1023 {
            font-size: 24px;
        }

        @media screen and (max-width: 520px) {
            font-size: 16px;
            transform: rotate(3deg);
            padding: 10px 20px;
            margin-bottom: 40px;
        }
    }

    &__bottom {
        display: flex;
        gap: 40px;
        align-items: center;

        @include vp-767 {
            flex-direction: column;
        }

        @media screen and (max-width: 520px) {
            width: 100%;
        }
    }

    &__image {
        width: 360px;
        height: 430px;

        @media screen and (max-width: 520px) {
            width: 300px;
            height: 380px;
        }

        @include vp-389 {
            width: 220px;
            height: 290px;
        }

        @media screen and (max-width: 340px) {
            width: 190px;
            height: 250px;
        }
    }

    &__image img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__info-block {
        display: flex;
        flex-direction: column;
        gap: 36px;
        align-self: flex-end;
        align-items: flex-start;
        max-width: 335px;
        width: 100%;
        margin-bottom: 60px;

        @media screen and (max-width: 520px) {
            gap: 20px;
            max-width: 100%;
        }
    }

    &__info-text {
        font-family: Inter;
        font-weight: 500;
        font-style: Medium;
        font-size: 18px;
        leading-trim: NONE;
        line-height: 140%;
        letter-spacing: 0%;
        color: rgba(255, 255, 255, 1);
        margin: 0;

        @include vp-767 {
            text-align: center;
        }

        @media screen and (max-width: 520px) {
            font-size: 16px;
            max-width: 210px;
            margin: 0 auto;
        }
    }

    &__btn {
        background: rgba(116, 200, 8, 1);
        max-width: 300px;
        width: 100%;
        border-radius: 60px;
        padding: 15px;
        font-family: Coolvetica;
        font-weight: 400;
        font-style: Regular;
        font-size: 30px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0.5px;
        text-align: center;
        vertical-align: middle;
        color: rgba(60, 43, 139, 1);
        transition: .3s;

        @include vp-767 {
            max-width: 100%;
        }

        @media screen and (max-width: 520px) {
            background: rgba(185, 227, 131, 1);
            font-size: 16px;
        }
    }

    &__btn:hover {
        opacity: 0.8;
    }

}