.tape {
    overflow: hidden;
    position: relative;
    z-index: 5;
    &__wrapper {
        padding: 0;
    }
    &__slider {
        
        
        overflow: hidden;
        background: rgba(60, 43, 139, 1);
        padding: 17px 0;
    }

    .tape__slider-content {
        width: 100%;
        display: flex;
        align-items: center;
        gap: 20px;
        will-change: transform;
        position: relative;
        z-index: 1;
        backface-visibility: hidden; 
        transform-style: preserve-3d;
 
    }

    .swiper-slide {
        width: auto;
        display: flex;
        align-items: center;
        gap: 20px;
        p {
            font-family: Coolvetica;
            font-weight: 400;
            font-style: Regular;
            font-size: 20px;
            leading-trim: NONE;
            line-height: 24.8px;
            letter-spacing: 10%;
            vertical-align: middle;
            text-transform: uppercase;
            color: rgba(255, 255, 255, 1);
            margin: 0;

            @media screen and (max-width: 520px) {
                font-size: 16px;
            } 
        }

        svg {
            width: 8px;
            height: 8px;
            flex-shrink: 0;
        }
    }

    @keyframes marquee {
        0% {
            transform: translateX(0);
        }
        100% {
            transform: translateX(-100%); 
        }
    }

    &--angle-1 {
        transform: rotate(1deg);
        margin-top: -15px;
        padding-bottom: 15px;
    }

    &--angle-2 {
        transform: rotate(-1deg);
        margin-top: -15px;
        padding-bottom: 15px;
    }

    &--angle-3 {
        transform: rotate(1.5deg);
        margin-top: -30px;
        padding-bottom: 30px;

        @media screen and (max-width: 520px) {
            margin-top: -10px;
            padding-bottom: 10px;
        }
    }
}